<template>
  <div>
    <!-- begin breadcrumb -->
    <ol class="breadcrumb float-xl-right">
      <li class="breadcrumb-item"><a href="javascript:;">Home</a></li>
      <li class="breadcrumb-item"><a href="javascript:;">Setting</a></li>
      <li class="breadcrumb-item active">Ewon</li>
    </ol>
    <!-- end breadcrumb -->
    <!-- begin page-header -->
    <h1 class="page-header">Add Ewon Setting</h1>
    <!-- end page-header -->

    <!-- begin panel -->
    <panel title="Add Ewon">
      <form>
        <div class="form-group row m-b-15">
          <label class="col-form-label col-md-2">Topic</label>
          <div class="col-md-10">
            <input
              type="input"
              class="form-control m-b-5"
              placeholder="Enter Topic"
              name="topic"
              v-model="topic"
            />
          </div>
        </div>
        <div class="form-group row m-b-15">
          <label class="col-form-label col-md-2">Type Topic</label>
          <div class="col-md-10">
            <v-select
              :options="data_types"
              v-model="tipe_data"
              placeholder="Select Type Data Topic"
            >
            </v-select>
          </div>
        </div>
        <b-button
          class="float-right mb-3"
          variant="primary"
          @click="create()"
          v-if="url == 'add'"
          >Create</b-button
        >
        <b-button
          class="float-right mb-3"
          variant="primary"
          @click="create()"
          v-else
          >Edit</b-button
        >
      </form>
    </panel>
    <!-- end panel -->
  </div>
</template>

<script>
import PageOptions from "../../config/PageOptions.vue";

export default {
  name: "add-ewon",
  data() {
    return {
      topic: "",
      type_topic: "",
      data_types: ["text", "number"],
      url: "",
      tipe_data: "",
      topicID: "",
    };
  },
  created() {
    var currentUrl = this.$route.path.split("/");
    this.topicID = currentUrl[4];
    this.url = currentUrl[3];
    PageOptions.pageWithFooter = true;
  },
  beforeRouteLeave(to, from, next) {
    PageOptions.pageWithFooter = false;
    next();
  },
  methods: {
    create() {
      const body = {
        topic: this.topic,
        type_topic: this.tipe_data,
      };

      if (this.url == "add") {
        this.$axios
          .post("/setting/ewon", body, {
            headers: {
              "Content-Type": "application/json",
            },
          })
          .then(() => {
            this.$notify({
              title: `Insert Data Success`,
              text: `Success`,
              type: "success",
            });

            setTimeout(() => {
              this.$router.push("/setting/ewon");
            }, 1500);
          })
          .catch((err) => {
            this.$notify({
              title: `Insert Data Failed : ${err}`,
              text: `Error`,
              type: "error",
            });
          });
      } else {
        this.$axios
          .put("/setting/ewon/" + this.topicID, body, {
            headers: {
              "Content-Type": "application/json",
            },
          })
          .then(() => {
            this.$notify({
              title: `Update Data Success`,
              text: `Success`,
              type: "success",
            });

            setTimeout(() => {
              this.$router.push("/setting/ewon");
            }, 1500);
          })
          .catch((err) => {
            this.$notify({
              title: `Update Data Failed : ${err}`,
              text: `Error`,
              type: "error",
            });
          });
      }
    },
    getData() {
      if (this.url == "edit") {
        const url = "/setting/ewon/" + this.topicID;
        this.$axios
          .get(url)
          .then((response) => {
            this.topic = response.data.data.txtTopic;
            this.type_topic = response.data.data.txtTypeTopic;
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
  },
  mounted() {
    this.getData();
  },
};
</script>